<template>
    <div class="config-page">
      <h1>Configurao do Hero Section</h1>
      <div>
        <h2>Imagens</h2>
        <div v-for="(image, index) in heroStore.images" :key="index">
          <input v-model="image.src" placeholder="URL da Imagem">
          <input v-model="image.alt" placeholder="Texto Alternativo">
        </div>
      </div>
      <div>
        <h2>Textos</h2>
        <div v-for="(text, index) in heroStore.texts" :key="index">
          <input v-model="text.h1" placeholder="Ttulo">
          <input v-model="text.p" placeholder="Subttulo">
        </div>
      </div>
      <div>
        <h2>Botes</h2>
        <label>
          <input type="checkbox" v-model="heroStore.showButtons"> Mostrar Botes
        </label>
      </div>
      <button @click="saveConfig">Salvar Configurao</button>
    </div>
  </template>
  
  <script>
  import { useHeroStore } from '@/store';
  
  export default {
    name: 'ConfigPage',
    setup() {
      const heroStore = useHeroStore();
      return { heroStore };
    },
    methods: {
      saveConfig() {
        this.heroStore.updateHeroConfig(this.heroStore);
        alert('Configurao salva com sucesso!');
      }
    }
  };
  </script>
  
  <style scoped>
  .config-page {
    padding: 20px;
  }
  </style>