// store.js
import { createStore } from 'vuex';

export default createStore({
  state: {
    heroConfig: {
      images: [
        { src: require('@/assets/images/banners/hero6.jpg'), alt: 'Team' },
        { src: require('@/assets/images/banners/hero2.jpg'), alt: 'Collaboration' },
        { src: require('@/assets/images/banners/hero3.jpg'), alt: 'Team Meeting' },
        { src: require('@/assets/images/banners/hero4.jpg'), alt: 'Innovation' }
      ],
      texts: [
        { 
          h1: 'Transformando Ideias Em <span class="highlight">Solues Inovadoras</span>', 
          p: 'Solues de TI e design personalizados para o seu <span class="highlight">negcio</span>.' 
        },
        { 
          h1: 'A <span class="highlight">Colaborao</span>  o Futuro para sua Empresa', 
          p: 'Trabalhando juntos para criar solues de <span class="highlight">sucesso</span>.' 
        },
        { 
          h1: 'Inovao e o <span class="highlight">Futuro para sua empresa </span>', 
          p: 'Melhore a performance com novas solues <span class="highlight">tecnolgicas</span>.' 
        },
        { 
          h1: 'Transforme sua Empresa com <span class="highlight">Tecnologia</span>', 
          p: 'Criamos a tecnologia de amanh para o seu <span class="highlight">negcio</span> de hoje.' 
        }
      ],
      showButtons: true
    }
  },
  mutations: {
    updateHeroConfig(state, newConfig) {
      state.heroConfig = newConfig;
    },
    toggleButtons(state) {
      state.heroConfig.showButtons = !state.heroConfig.showButtons;
    }
  },
  actions: {
    updateHeroConfig({ commit }, newConfig) {
      commit('updateHeroConfig', newConfig);
    },
    toggleButtons({ commit }) {
      commit('toggleButtons');
    }
  }
});